
.Container_all {
    display: flex;
    justify-content: space-between;
    height: 100%;
  }
  
 
  
  .Container_all__text {
    color: rgb(255, 255, 255);
    font-size: 1.2em;
    font-family: cursive;
    background-color: #001437d1;
    flex: 1;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: relative;
  
    h1 {
      font-size: 22px;
      font-weight: normal;
      margin-left: -55px;
  
    }
  
    h2 {
      font-size: 30px;
      margin-top: 10px;
    }
  
    img {
      width: 100%;
      height: 100%;
      z-index: -1;
      position: absolute;
    }
  }
  .reseau_sociaux{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    height: 100px;
    margin-top: 20px;
    margin-bottom: 20px;
    background: #001437d1;
    font-size: 1.2em;
    i{
      color:  #fff;
      transition: all 0.3s ease-in-out;
      &:hover{
        color: #00296f;
      }
    }
  }