@use "../animation/animation" as *;

.container-home-page {
  height: 100vh;
  width: 100%;
  position: relative;
  background-color: #001437e0;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  font-size: 10px;


  .middle-block {
    background-color: rgba(0,20,55,.8);
    cursor: pointer;
    height: 60px;
    margin-right: -73px;
    -webkit-transform: rotate(0deg) scale(1.5);
    transform: rotate(0deg) scale(1.5);
    transition: all .3s ease-in-out;
    width: 63px;
  }

  #nav{
    a{
      font-size: 23px;
      
    }
  }





  img {
    // width: 100%;
    height: 100%;
    z-index: -1;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

}