.home__cv {
    background-color: darken($color: #00296f, $amount: 11);
    // height: 100%;
    position: relative;
    height: 97vh;
    flex: 2;
}

.main__cv {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.bg-footer {
    position: absolute;
    bottom: 0;
    background-repeat: no-repeat;
    width: 100%;
}

.main__cv__content {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    height: 400px;
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 20px;

    article {
        img {

            width: 326px;
            height: 447px;
            margin-bottom: -65px;
        }
    }

    aside {
        text-align: center;

        button {
            background-color: #00296f;
            color: #fff;
            border: none;
            padding: 10px;
            border-radius: 5px;
            font-size: 16px;
            font-weight: bold;
            cursor: pointer;
            transition: all 0.3s ease-in-out;
            margin-top: 20px;

            &:hover {
                background-color: #fff;
                color: #00296f;
            }
        }
    }
}