header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: darken($color: #00296f, $amount: 11);
    padding: 20px;
    width: 100%;


    h1 {
        font-size: 1em;
        font-family: cursive;
    }


}

ul {
    display: flex;
    align-items: center;
    transition: all 5s ease-in-out;

    li {
        margin-right: 1.7em;

        a{
            cursor: pointer;
            font-size: 18px;
            font-weight: bold;
            padding: 5px 10px;
            transition: all 0.2s ease-in-out;
            border-radius: 10px;
            color: #fff;

            &:hover {
                color: darken($color: #00296f, $amount: 11);
                background-color: #fff;
    
    
            }
        }






    }

    .active {
        color: darken($color: #00296f, $amount: 11);
        background-color: #fff;
    }
}